import React from "react";
import Estudio from "../../../image/pexels-karolina-grabowska-7681097.jpg";
import { motion } from "framer-motion";

function OutsourcingText() {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      viewport={{ once: true }}
      className="outsourcing-text-container container"
    >
      <div className="fila row">
        <div className="imagen col-12 col-lg-6">
          <h2>Por qu contratar nuestro servicio de outsourcing contable?</h2>
          <motion.img
            initial={{ opacity: 0, scale: 0 }}
            whileInView={{ opacity: 1, scale: 1, transition: { duration: 1 } }}
            transition={{ duration: 0.5 }}
            viewport={{ once: true }}
            src={Estudio}
            alt=""
          />
        </div>
        <div className="col-12 col-lg-6 ">
          <p>
            Nuestra experiencia en el sector nos permite disear un plan a la
            medida de cada uno de nuestros clientes. Con nuestra empresa, usted
            puede tercerizar parcial o integralmente los servicios contables;
            por ejemplo:
          </p>
          <ul>
            <li>Actualizacin y llevado de libros contables.</li>
            <li>Conciliaciones bancarias y arqueos de caja.</li>
            <li>Inventarios de existencias y activos fijos.</li>
            <li>
              Emisin de comprobantes de pago (facturas, boletas, notas de
              crdito, otros).
            </li>
            <li>
              Elaboracin de planilla de trabajadores y emisin de boletas de
              pago.
            </li>
            <li>
              Declaracin de PDT Plame y declaracin de planillas a la AFP.
            </li>
            <li>
              Elaboracin de liquidaciones de beneficios sociales a los
              trabajadores.
            </li>
            <li>Elaboracin de contratos de trabajo.</li>
            <li>Liquidacin y declaracin de impuestos.</li>
            <li>Elaboracin de Estados Financieros y notas.</li>
            <li>Elaboracin de Estados Financieros consolidados.</li>
            <li>
              Elaboracin y presentacin de la Declaracin Jurada Anual del
              impuesto a la renta.
            </li>
            <li>Auditora y revisin de Estados Financieros.</li>
            <li>Auditora interna y externa.</li>
            <li>Atencin a fiscalizaciones y requerimientos de Sunat.</li>
            <li>Registro de operaciones econmicas en software contable.</li>
            <li>
              Pagos de obligaciones (proveedores, servicios, tributos,
              prstamos)
            </li>
            <li>Elaboracin de proyectos de negocio.</li>
            <li>Elaboracin de presupuestos y flujo de caja proyectado</li>
            <li>Planeamiento tributario.</li>
          </ul>
        </div>
      </div>
    </motion.div>
  );
}

export default OutsourcingText;
