import React from "react";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";

function AsesoriaContableWelcome() {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      viewport={{ once: true }}
      className="AsesoriaContableWelcome-container"
    >
      <div className="container">
        <h3>Asesora contable</h3>
        <p>
          Concntrese en las actividades principales de su negocio! Le
          <span>ofrecemos asesora contable y tributaria</span> de la mano de
          los mejores profesionales!{" "}
        </p>
        <p>
          Con nuestro servicio de <span>asesora contable</span> todos los
          aspectos contables de su empresa marcharn conforme a las regulaciones
          vigentes. Reduzca errores en declaraciones y pagos y cuente con los
          mejores especialistas contables y con las ltimas tecnologas del
          sector, a su servicio!
        </p>
        <Link to="/Contacto">
          <motion.button whileHover={{ scale: 1.2 }}>Contctanos</motion.button>
        </Link>
      </div>
    </motion.div>
  );
}

export default AsesoriaContableWelcome;
