import { motion } from "framer-motion";
import React from "react";
import { Link } from "react-router-dom";

function AsesoriaAdministrativaWelcome() {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      viewport={{ once: true }}
      className="AsesoriaAdministrativaWelcome-container "
    >
      <div className="container">
        <h3>Asesora Administrativa para empresas</h3>
        <p>
          Es momento de que vea crecer su negocio! Concntrese en su core
          business principal y contrate nuestros
          <span> servicios de asesora administrativa.</span>
        </p>
        <p>
          Gracias a nuestra <span>asesora administrativa </span>
          usted tendr un mejor manejo de su empresa. Tener en orden su negocio
          har que la toma de decisiones sea ms rpida y acertada!
        </p>
        <p>
          Con <span> asesora administrativas</span> no tendr que preocuparse
          ms por la gestin de recursos internos y externos de su
          emprendimiento, ni en trmites, licencias, manejo de personal, etc.
        </p>
        <Link to="/contacto">
          <motion.button whileHover={{ scale: 1.2 }}>Contctanos</motion.button>
        </Link>
      </div>
    </motion.div>
  );
}

export default AsesoriaAdministrativaWelcome;
