import React from "react";
import AsesoriaTributaria from "../../../image/pexels-anna-nekrashevich-6801680.jpg";
import AsesoriaFinanciera from "../../../image/pexels-mikhail-nilov-7681746.jpg";
import { motion } from "framer-motion";

function AsesoriaServicios() {
  return (
    <div className="asesoria-servicios-container container">
      {/* First */}

      <motion.div
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
        viewport={{ once: true }}
        className="first row"
      >
        <div className="col-lg-6 col-12">
          <h3>Servicio de asesora tributaria</h3>
          <p>
            Gracias al <span>servicio de asesora tributaria</span>, podemos
            ayudarle a analizar, establecer e implementar los controles que su
            negocio necesite para cumplir con las normas tributarias del pas.
            Nosotros le proporcionaremos las herramientas legales que su negocio
            necesita para laborar de acuerdo con la ley, en asuntos como:
          </p>
          <ul>
            <li>Cdigo tributario.</li>
            <li>Ley de impuesto a la renta.</li>
            <li>Ley de comprobantes de pago.</li>
            <li>Ley del impuesto general de ventas.</li>
            <li>Retenciones.</li>
            <li>Percepciones.</li>
            <li>Detracciones.</li>
            <li>Drawback.</li>
            <li>Libros electrnicos.</li>
            <li>Comprobantes de pagos electrnicos, entre otros.</li>
          </ul>
        </div>
        <div className="col-lg-6 col-12">
          <motion.img
            initial={{ opacity: 0, scale: 0 }}
            whileInView={{ opacity: 1, scale: 1, transition: { duration: 1 } }}
            transition={{ duration: 0.5 }}
            viewport={{ once: true }}
            src={AsesoriaFinanciera}
            alt=""
          />
        </div>
      </motion.div>

      {/* SECOND */}

      <motion.div
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
        viewport={{ once: true }}
        className="second"
      >
        <h3>Servicio de asesora contable y tributaria</h3>
        <p>
          Con el <span>servicio de asesora contable y tributaria</span> su
          empresa puede aprovechar los <span>beneficios tributarios</span>.
          Estos son incentivos que proporciona el Estado con la finalidad de que
          se alcancen objetivos econmicos y sociales en pro del crecimiento y
          desarrollo del territorio.
        </p>
        <p>
          Por ejemplo, existen incentivos en deducciones al Impuesto a la Renta
          para aquellos negocios que contraten personal con discapacidad o que
          inviertan en innovacin. En resumen, los incentivos son la suma de
          exoneraciones, deducciones y tratamientos tributarios especiales que
          repercuten en una reduccin de las obligaciones tributarias de los
          contribuyentes que califiquen para este beneficio. Conocer si su
          empresa aplica para estos incentivos es un factor clave para reducir
          el pago de tributos, dentro de las normas vigentes.{" "}
        </p>
        <p>
          Contar con <span>asesora tributaria</span> tambin evita retrasos y
          errores en la declaracin de impuestos que traigan como consecuencia
          sanciones para su compaa. Por ejemplo, multas por declarar cifras o
          datos falsos; el cierre temporal del establecimiento o de las oficinas
          donde se ofrezcan los servicios; suspensin de licencias autorizadas
          por el Estado, entre otras.
        </p>
      </motion.div>

      {/* THIRD */}

      <motion.div
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
        viewport={{ once: true }}
        className="third row"
      >
        <div className="col-lg-6 col-12">
          <h3>
            Por qu optar por el servicio de asesora tributaria para su
            empresa?
          </h3>
          <motion.img
            initial={{ opacity: 0, scale: 0 }}
            whileInView={{ opacity: 1, scale: 1, transition: { duration: 1 } }}
            transition={{ duration: 0.5 }}
            viewport={{ once: true }}
            src={AsesoriaTributaria}
            alt=""
          />
        </div>

        <div className="col-lg-6 col-12">
          <p>
            Qfrecemos el servicio de asesora tributaria con el objetivo de que
            sus declaraciones mensuales y DJ anual no sean observados por SUNAT.
            De la misma forma, con el trabajo de nuestro equipo de contadores
            profesionales, si acaso su negocio fuera fiscalizado, pasara la
            evaluacin sin contratiempos, evitando multas por irregularidades u
            omisiones. Ser un buen contribuyente tambin trae beneficios
            adicionales para su empresa, entre ellos:
          </p>
          <ul>
            <li>
              Acceder a un cronograma especial para declarar y pagar
              obligaciones mensuales.
            </li>
            <li>
              Que no se efecten retenciones del Impuesto General de las Ventas.
            </li>
            <li>La posibilidad de fraccionar sus deudas tributarias.</li>
            <li>
              Atencin preferencial en solicitudes de devoluciones de saldos a
              favor.
            </li>
          </ul>
        </div>

        <div>
          <p>
            Existen empresas que por cometer infracciones tributarias (o incluso
            delitos, en muchos casos sin saberlo) terminan cerrando o con deudas
            que no pueden saldar. Esto ocurre porque no conocen cmo funcionan
            los tributos en el pas o no se actualizan sobre las modificaciones
            que se aaden peridicamente a las normativas vigentes; en
            consecuencia, muchas pymes y mypes terminan cerrando. Su negocio no
            puede permitirse detener su crecimiento, cerrar ni tener sanciones,
            por eso le ofrecemos <span>asesora tributaria</span> la que
            necesita tanto de forma preventiva como en inspecciones fiscales o
            procesos legales en va administrativa o judicial.
          </p>
          <p>
            Nuestra empresa de <span>asesora tributaria </span>
            cuenta con el equipo de <span>contadores profesionales</span> de
            amplia experiencia que usted necesita para encargarse de sus asuntos
            tributarios en Per.
          </p>
          <p>
            Contamos con ms de 10 aos de trayectoria, trabajando con grandes
            compaas de diferentes sectores empresariales, por lo que ofrecemos
            un servicio acorde de las necesidades de cada cliente.
          </p>
        </div>
      </motion.div>
    </div>
  );
}

export default AsesoriaServicios;
