import React from "react";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";

function OutsourcingWelcome() {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      viewport={{ once: true }}
      className="outsourcingwelcome-container "
    >
      <div className="container">
        <h2>Empresa de outsourcing contable</h2>
        <p>
          Necesita el <span>servicio de outsourcing contable</span> ? Existen
          muchas <span>empresas de outsourcing contable</span>, pero nosotros no
          solo le ofrecemos un servicio, nos volvemos su aliado estratgico para
          que pueda concentrarse en el core business de su negocio. Tenemos ms
          de 10 aos de experiencia trabajando con empresas de diferentes rubros
          y sectores! Nuestra amplia cartera de clientes, nos respalda!
        </p>
        <Link to="/Contacto">
          <motion.button whileHover={{ scale: 1.2 }}>Contctanos</motion.button>
        </Link>
      </div>
    </motion.div>
  );
}

export default OutsourcingWelcome;
