import React from "react";
import { motion } from "framer-motion";

import conocenosContador from "../../image/pexels-mikhail-nilov-8297478.jpg";

import Icono1 from "../../image/3289557-clan-group-partners-peers-people_107100.png";
import Icono2 from "../../image/flag_icon-icons.com_71193.png";
import Icono3 from "../../image/paper-airplane_icon-icons.com_50080.png";

function Info() {
  return (
    <div className="info-container container">
      {/* Texto */}

      <motion.div
        className="row texto container-fluid"
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
        viewport={{ once: true }}
      >
        <div className="col-12 col-lg-6">
          <motion.img
            initial={{ opacity: 0, scale: 0 }}
            whileInView={{ opacity: 1, scale: 1, transition: { duration: 1 } }}
            transition={{ duration: 0.5 }}
            viewport={{ once: true }}
            src={conocenosContador}
            alt=""
          />
        </div>
        <div className="col-12 col-lg-6">
          <p>
            Somos un <span>Estudio Contable</span> con ms de 10 aos de
            experiencia en el mercado.
          </p>
          <p>
            Brindamos soluciones en
            <span>
              Asesora Contable, Asesora Tributaria, Asesora Laboral, Asesora
              Financiera, Asesora Administrativa y Outsourcing Contable
            </span>
            a Micro y Pequeas en empresas <span>(Mypes Pymes)</span>; que
            inician operaciones o ya tienen presencia en el mercado, ofreciendo
            un soporte y acompaamiento para el logro de sus objetivos
            contables, tributarios y financieros.
          </p>
          <p>
            Nuestro estudio contable cuenta con profesionales con gran
            trayectoria y experiencia en empresas de gran envergadura de
            diferentes sectores de negocio, como construccin, minera,
            servicios, educacin, retail, comerciales y salud, por lo que
            nuestro <span>estudio contable</span> tiene la capacidad y los
            conocimientos
            <span>(know-how)</span> adecuados para brindar soluciones en el
            menor tiempo y costo, superando las expectativas de nuestros
            clientes Mypes y pymes.
          </p>
          <p>
            Nuestros principales clientes son micro y pequeas empresas,
            principalmente nuevas en el mercado, es por ello que los asesoramos
            desde su constitucin hasta la puesta en marcha de su empresa.
            Buscamos ser socios estratgicos con nuestros clientes, en el
            sentido de estar siempre disponible para cualquier tema relacionado
            con su negocio. Contamos con profesionales con experiencia y con un
            enfoque al servicio al cliente. Creemos firmemente que nos debemos a
            nuestros clientes, por ello son el foco de nuestras acciones, las
            cuales estn orientadas a satisfacer sus necesidades con un servicio
            de calidad. Somos una empresa muy flexible por lo que nos adaptamos
            a las necesidades de nuestros clientes.{" "}
          </p>
        </div>
      </motion.div>

      {/* Items */}

      <motion.div
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
        viewport={{ once: true }}
        className="row items container-fluid"
      >
        <div className="col-lg-4">
          <img src={Icono1} alt="" />
          <h3>Nuestros Principios</h3>
          <p>1-Compromiso</p>
          <p>2-Integridad</p>
          <p>3-Trabajo en equipo</p>
          <p>4-Honestidad</p>
          <p>5-Respeto</p>
        </div>
        <div className="col-lg-4">
          <img src={Icono2} alt="" />
          <h3>Misin</h3>
          <p>
            Ser reconocida como una empresa de consultora que brinda
            soluciones prcticas, oportunas, flexibles, a la medida y exigencia
            de nuestros clientes, superando con satisfaccin las expectativas en
            cuanto a calidad, costo y tiempo"
          </p>
        </div>
        <div className="col-lg-4">
          <img src={Icono3} alt="" />
          <h3>Visin</h3>
          <p>
            Brindar soporte y soluciones en temas contables, tributarios y
            empresariales a micro, pequeas y medianas empresas establecidas en
            el Per, con profesionales con experiencia comprobada, en constante
            capacitacin y actualizacin, con el objetivo de agregar valor a las
            operaciones de nuestros clientes, colaboradores, accionistas y
            sociedad."
          </p>
        </div>
      </motion.div>
    </div>
  );
}

export default Info;
